import { h, Component } from 'preact';
import { Link } from 'preact-router/match';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkState from 'linkstate';
import { route } from 'preact-router';

import style from './style.css';


const initialState = {
    menu: {
		profile: false,
		main: false,
	},
	searchText: '',
};

@observer
class Leftmenu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	handleLinkClick = (e) => {
		const { menu } = this.state;
		let menuName = e.target.dataset.name;
		if (!menuName && e.target.parentNode) {
			menuName = e.target.parentNode.dataset.name;
		}
		if (menuName) {
			menu[menuName] = !menu[menuName];
		}
		this.setState({ menu });
	}

    toggleMenu = (e) => {
        e.preventDefault();
        const { appState } = this.props.stores;
        appState.toggleMenu();
    };

	search = () => {
		const { searchText } = this.state;
		route(`/search/${searchText}`, true);
	}

    render() {
        const { appState } = this.props.stores;
        const { darkmode, menu } = appState;

		return (
			<nav class={`navbar navbar-inverse navbar-fixed-top ${style['sidebar-wrapper']} ${darkmode ? style.darkmode : ''} ${menu ? style['sidebar-wrapper-visible'] : ''}`}>
                <ul class={`nav ${style['sidebar-nav']}`}>
                    <li class='mb-2'>
                        <a class='nav-link' href='#' onClick={this.toggleMenu}><i class={`fas ${menu ? 'fa-long-arrow-alt-left' : 'fa-long-arrow-alt-right'}`} /></a>
                    </li>

                    <li>
                        <Link className='nav-link' activeClassName={style['sidebar-nav-active']} href='/'
                            data-name='main'
                            onClick={this.handleLinkClick}
                        ><i class='fab fa-hubspot mr-3' /> Home</Link>
                    </li>
                    <li>
                        <Link className='nav-link' activeClassName={style['sidebar-nav-active']} href='/weather'
                            data-name='main'
                            onClick={this.handleLinkClick}
                        ><i class='fas fa-cloud-sun-rain mr-3' /> Weather</Link>
                    </li>
                    <li>
                        <Link className='nav-link' activeClassName={style['sidebar-nav-active']} href='/demand'
                            data-name='main'
                            onClick={this.handleLinkClick}
                        ><i class='fas fa-hand-holding-usd mr-3' /> Demand</Link>
                    </li>
                    <li>
                        <Link className='nav-link' activeClassName={style['sidebar-nav-active']} href='/prices'
                            data-name='main'
                            onClick={this.handleLinkClick}
                        ><i class='fas fa-chart-line mr-3' /> Electricity prices</Link>
                    </li>
                    <li>
                        <Link className='nav-link' activeClassName={style['sidebar-nav-active']} href='/production'
                            data-name='main'
                            onClick={this.handleLinkClick}
                        ><i class='fas fa-chart-bar mr-3' /> Production</Link>
                    </li>
                    <li>
                        <Link className='nav-link' activeClassName={style['sidebar-nav-active']} href='/wearstatus'
                            data-name='main'
                            onClick={this.handleLinkClick}
                        ><i class='fas fa-battery-three-quarters mr-3' /> Wear status</Link>
                    </li>
                    <li>
                        <Link className='nav-link' activeClassName={style['sidebar-nav-active']} href='/wear'
                            data-name='main'
                            onClick={this.handleLinkClick}
                        ><i class='fas fa-thermometer mr-3' /> Wear</Link>
                    </li>
                    <li>
                        <Link className='nav-link' activeClassName={style['sidebar-nav-active']} href='/strategy'
                            data-name='main'
                            onClick={this.handleLinkClick}
                        ><i class='fas fa-chess-knight mr-3' /> Strategy</Link>
                    </li>
                    <li>
                        <Link className='nav-link' activeClassName={style['sidebar-nav-active']} href='/parkmanagement'
                            data-name='main'
                            onClick={this.handleLinkClick}
                        ><i class='fas fa-cog mr-3' /> Park Management</Link>
                    </li>
                    <li>
                        <Link className='nav-link' activeClassName={style['sidebar-nav-active']} href='/bid'
                            data-name='main'
                            onClick={this.handleLinkClick}
                        ><i class='fas fa-money-bill-alt mr-3' /> Bid</Link>
                    </li>

                </ul>
            </nav>
		);
	}
}

export default Leftmenu;
