import { observable, configure, action, computed } from 'mobx';
import StoreModel from 'preact-storemodel';
import util from 'preact-util';
import { route } from 'preact-router';
import PubSub, { topics } from '../lib/pubsub';

configure({ enforceActions: 'always' });

class UserStore extends StoreModel {
    constructor() {
        super('user', {
            namePlural: 'users',
            sort: 'title',
            limit: 100,
            api: {
                search: {
                    url: '/api/users/',
                    params: {
                        extendedView: 1,
                        limit: 15,
                        sort: 'id',
                    },
                },
                load: {
                    url: '/api/users/',
                    params: {},
                },
                save: {
                    url: '/api/users/',
                    params: {},
                },
            },
        });
    }

    @observable user = {};

    @observable users = [];

    @observable team = {};

    @observable teams = [];

    @observable dogs = [];

    @observable mushers = [];

    @observable workouts = [];

    @observable tracks = [];

    @observable types = [];

    @observable equipments = [];

    @observable workoutSummary = [];

    @observable emailIsAvailable = false;

    @observable emailMessage = '';

    @observable emailMessageIcon = '';

    @observable emailError = '';

    @observable emailErrorIcon = '';

    @observable register = {
        messageIcon: '',
        message: '',
        errorIcon: '',
        error: '',
    };

    @observable login = {
        showToken: false,
        messageIcon: '',
        message: '',
        errorIcon: '',
        error: '',
    };

    @observable loginLink = {
        emailSent: false,
        messageIcon: '',
        message: '',
        errorIcon: '',
        error: '',
    };

    @action
    updateField(key, value) {
        this[key] = value;
    }

    @action
    updateObjectField(object, key, value) {
        this[object][key] = value;
    }

    @action
    resetRegisterMessages() {
        this.register = {
            messageIcon: '',
            message: '',
            errorIcon: '',
            error: '',
        };
    }

    @action
    resetLoginMessages() {
        this.login = {
            showToken: false,
            messageIcon: '',
            message: '',
            errorIcon: '',
            error: '',
        };
    }

    @action
    resetLoginLinkMessages() {
        this.loginLink = {
            emailSent: this.loginLink.emailSent,
            messageIcon: '',
            message: '',
            errorIcon: '',
            error: '',
        };
    }

    async registerUser({ email, password, fingerprint, jwtToken }) {
        const isValidEmail = util.validateEmail(email);
        if (isValidEmail) {
            const registerResponse = await util.fetchApi('/api/users/register', { publish: true, method: 'POST' }, { email, password, fingerprint, jwtToken });
            switch (registerResponse.status) {
                case 201:
                    this.updateField('user', registerResponse.data.user);
                    this.updateField('emailIsAvailable', true);
                    this.updateField('emailMessage', 'Thank you! You are now registered.');
                    this.updateObjectField('loginLink', 'emailSent', false);
                    util.setJwtToken(registerResponse.data.apiToken);
                    util.setUserEmail(registerResponse.data.user.email);
                    PubSub.publish(topics.JWT_TOKEN_CHANGED, registerResponse.data.apiToken);
                    break;
                case 401:
                    this.updateObjectField('register', 'errorIcon', 'fas fa-bomb');
                    this.updateObjectField('register', 'error', `Registration failed! ${registerResponse.status}: ${registerResponse.message}`);
                    break;
                default:
                    this.updateObjectField('register', 'errorIcon', 'fas fa-bomb');
                    this.updateObjectField('register', 'error', `Registration failed! ${registerResponse.status}: ${registerResponse.message}`);
                    break;
            }
        }
    }

    async verifyEmail({ token }) {
        const verifyResponse = await util.fetchApi('/api/users/verify/email/', { publish: true, method: 'POST' }, { token });
        switch (verifyResponse.status) {
            case 200:
                this.updateField('emailMessageIcon', 'fas fa-user-check');
                this.updateField('emailMessage', 'Email is now verified ');
                break;
            default:
                this.updateField('emailErrorIcon', 'fas fa-bomb');
                this.updateField('emailError', `Email verification failed! ${verifyResponse.status}: ${verifyResponse.message}`);
                break;
        }
    }

    async checkEmail(email) {
        const isValidEmail = util.validateEmail(email);
        if (isValidEmail) {
            const emailCheckResponse = await util.fetchApi('/api/users/check', { publish: true }, { email });
            switch (emailCheckResponse.status) {
                case 200:
                    this.updateField('emailIsAvailable', true);
                    this.updateField('emailMessage', 'Email is available ');
                    break;
                case 403:
                    this.updateField('emailIsAvailable', false);
                    this.updateField('emailMessage', 'Email is already registered. Try forgot password function on the login page...');
                    break;
                default:
                    this.updateField('emailIsAvailable', false);
                    this.updateField('emailMessage', '');
                    break;
            }
        }
    }

    async checkLogin({ email, password, token }) {
        const loginResponse = await util.fetchApi('/api/users/login', { method: 'POST', publish: true }, { email, password, token });
        switch (loginResponse.status) {
            case 200:
                this.updateField('user', loginResponse.data.user);
                util.setJwtToken(loginResponse.data.apiToken);
                util.setUserEmail(loginResponse.data.user.email);
                PubSub.publish(topics.JWT_TOKEN_CHANGED, loginResponse.data.apiToken);
                break;
            case 202:
                if (loginResponse.needsTwoFactor) {
                    this.updateObjectField('login', 'message', '2FA token is required to login.');
                    this.updateObjectField('login', 'showToken', true);
                }                
                break;
            case 403:
                if (loginResponse.message === 'Wrong password!') {
                    this.updateObjectField('login', 'errorIcon', 'fas fa-exclamation-triangle');
                    this.updateObjectField('login', 'error', `Wrong password! If you have forgotten your password you can always use the reset password function.`);
                } else {
                    this.updateObjectField('login', 'errorIcon', 'fas fa-search');
                    this.updateObjectField('login', 'error', `User not found! You are welcome to register if you don't have an account  \nGo to register to continue.`);
                }
                break;
            case 422:
                this.updateObjectField('login', 'errorIcon', 'fas fa-exclamation-triangle');
                this.updateObjectField('login', 'error', `Login failed! Missing info in some of the fields below.`);
                break;
            default:
                this.updateObjectField('login', 'errorIcon', 'fas fa-bomb');
                this.updateObjectField('login', 'error', `Login failed! ${loginResponse.status}: ${loginResponse.message}`);
                break;
        }
    }

    async getInfo() {
        const infoResponse = await util.fetchApi('/api/users/info', { publish: true, method: 'GET' }, {});
        switch (infoResponse.status) {
            case 200:
                this.updateField('user', infoResponse.data.user);
                // this.updateField('team', infoResponse.data.team);
                // this.updateField('teams', infoResponse.data.teams);
                // this.updateField('dogs', infoResponse.data.dogs);
                // this.updateField('mushers', infoResponse.data.members);
                // this.updateField('workouts', infoResponse.data.workouts);
                // this.updateField('tracks', infoResponse.data.tracks);
                // this.updateField('types', infoResponse.data.types);
                // this.updateField('equipments', infoResponse.data.equipments);
                // this.updateField('workoutSummary', infoResponse.data.workoutSummary);
                break;
            case 401:
                PubSub.publish(topics.LOG_OUT);
        		route('/', true);
                break;
            default:
                this.updateObjectField('register', 'errorIcon', 'fas fa-bomb');
                this.updateObjectField('register', 'error', `User info fetch failed! ${infoResponse.status}: ${infoResponse.message}`);
                break;
        }
    }

    async sendLoginLink(email) {
        const isValidEmail = util.validateEmail(email);
        if (isValidEmail) {
            const emailCheckResponse = await util.fetchApi('/api/users/login/link', { publish: true }, { email });
            switch (emailCheckResponse.status) {
                case 200:
                    this.updateObjectField('loginLink', 'emailSent', true);
                    this.updateObjectField('loginLink', 'messageIcon', 'fas fa-shipping-fast');
                    this.updateObjectField('loginLink', 'message', `Email sent!`);
                    break;
                default:
                    this.updateObjectField('loginLink', 'errorIcon', 'fas fa-bomb');
                    this.updateObjectField('loginLink', 'error', `Email sending failed! ${emailCheckResponse.status}: ${emailCheckResponse.message}`);
                    break;
            }
        }
    }

    async validateLoginCode(email, code) {
        const isValidCode = util.validateCode(code, 100000, 999999);
        if (isValidCode) {
            const loginResponse = await util.fetchApi('/api/users/login/code', { publish: true, method: 'POST' }, { email, code });
            switch (loginResponse.status) {
                case 200:
                    this.updateField('user', loginResponse.data.user);
                    this.updateObjectField('loginLink', 'emailSent', false);
                    util.setJwtToken(loginResponse.data.apiToken);
                    util.setUserEmail(loginResponse.data.user.email);
                    PubSub.publish(topics.JWT_TOKEN_CHANGED, loginResponse.data.apiToken);
                    break;
                case 403:
                    if (loginResponse.message === 'Wrong code!') {
                        this.updateObjectField('login', 'errorIcon', 'fas fa-exclamation-triangle');
                        this.updateObjectField('login', 'error', `Wrong one time code! Check your last email and try again.`);
                    } else if (loginResponse.message === 'Login code has expired!') {
                        this.updateObjectField('loginLink', 'emailSent', false);
                        this.updateObjectField('login', 'errorIcon', 'fas fa-exclamation-triangle');
                        this.updateObjectField('login', 'error', `One time code expired! Request a new one and try again.`);
                    } else {
                        this.updateObjectField('login', 'errorIcon', 'fas fa-bomb');
                        this.updateObjectField('login', 'error', `There is something wrong with your code! Request a new one and try again.`);
                    }
                    break;
                case 422:
                    this.updateObjectField('login', 'errorIcon', 'fas fa-exclamation-triangle');
                    this.updateObjectField('login', 'error', `Login failed! Missing info in some of the fields below.`);
                    break;
                default:
                    this.updateObjectField('login', 'errorIcon', 'fas fa-bomb');
                    this.updateObjectField('login', 'error', `Login failed! ${loginResponse.status}: ${loginResponse.message}`);
                    break;
            }
        }
    }
}

const store = new UserStore();
export default store;
